import { Hono } from "hono";
import { authMiddleware, authRouter } from "./auth";
import { paymentRouter } from "./payment/lemonsqueezy";
import { apiRouter } from "./api";
import { generateKey } from "./utils/key";
import { stManage } from './route/studentmanage';
import { r2ImageUploadRoute } from "./route/r2imageupload";
import { problemupload } from "./route/problemmanage";
const app = new Hono()
    .use(authMiddleware)
    .route("/", problemupload)
    .route("/", r2ImageUploadRoute)
    .route("/", stManage)
    .route("/", authRouter)
    // webhook handler
    .route("/", paymentRouter)
    // api routes
    .route("/api", apiRouter)
    .get("/", async (c) => {
    const user = c.get("user");
    const apiKey = await generateKey(user?.id, String(user?.lastKeyGeneratedAt?.getTime()), c.env.SECRET);
    const subscriptionLink = `${c.env.LEMONSQUEEZY_CHECKOUT_LINK}?checkout[email]=${user?.email}`;
    const subscriptionStatus = user?.subscriptionId
        ? "Premium"
        : `Free • <a href="${subscriptionLink}">Upgrade</a>`;
    return c.json({ message: "API server is running. Please use specific API endpoints." });
});
export default app;
