// backend-api-kit/src/route/studentmanage.ts
import { Hono } from 'hono';
import { db } from '../auth'; // DB 설정 가져오기
import { students } from '../db/schema';
import { eq } from 'drizzle-orm';
import { uuidv7 } from 'uuidv7'; // uuidv7 라이브러리 임포트
const app = new Hono();
export const stManage = app
    // GET /manage/student - 학생 목록 조회
    .get('/manage/student', async (c) => {
    const user = c.get("user");
    if (!user) {
        return c.json({ error: "인증되지 않은 사용자입니다." }, 401);
    }
    try {
        const studentList = await db(c.env)
            .select()
            .from(students)
            .where(eq(students.principal_id, user.id))
            .all();
        return c.json(studentList);
    }
    catch (error) {
        console.error("학생 조회 중 오류:", error);
        return c.json({ error: "학생 조회 중 오류가 발생했습니다." }, 500);
    }
})
    // POST /manage/student - 학생 추가 (UUID v7 사용)
    .post('/manage/student', async (c) => {
    const user = c.get("user");
    if (!user) {
        return c.json({ error: "인증되지 않은 사용자입니다." }, 401);
    }
    const body = await c.req.json();
    const { student_name, grade, guardian_phone, student_phone, tuition, subject, class: studentClass, teacher, school_name, kakao_name, guardian_kakao, admission_date, status, } = body;
    if (!student_name || !grade || !tuition || !subject || !status) {
        return c.json({ error: "필수 필드를 입력해야 합니다." }, 400);
    }
    try {
        const now = new Date();
        const studentData = {
            id: uuidv7(), // UUID v7으로 ID 생성
            student_name,
            grade,
            guardian_phone: guardian_phone || null,
            student_phone: student_phone || null,
            tuition: Number(tuition),
            subject,
            class: studentClass || null,
            teacher: teacher || null,
            school_name: school_name || null,
            kakao_name: kakao_name || null,
            guardian_kakao: guardian_kakao || null,
            admission_date: admission_date || null,
            status,
            principal_id: user.id,
            createdAt: now,
            updatedAt: now,
            discharge_date: null,
        };
        const [newStudent] = await db(c.env)
            .insert(students)
            .values(studentData)
            .returning();
        return c.json(newStudent, 201);
    }
    catch (error) {
        console.error("학생 추가 중 오류:", error);
        return c.json({ error: "학생 추가 중 오류가 발생했습니다." }, 500);
    }
})
    // PUT /manage/student/:id - 학생 수정 (ID를 문자열로 처리)
    .put('/manage/student/:id', async (c) => {
    const user = c.get("user");
    if (!user) {
        return c.json({ error: "인증되지 않은 사용자입니다." }, 401);
    }
    const id = c.req.param('id'); // 문자열로 처리 (parseInt 제거)
    const body = await c.req.json();
    try {
        const [updatedStudent] = await db(c.env)
            .update(students)
            .set({ ...body, updatedAt: new Date() })
            .where(eq(students.id, id))
            .returning();
        if (!updatedStudent) {
            return c.json({ error: "학생을 찾을 수 없습니다." }, 404);
        }
        return c.json(updatedStudent);
    }
    catch (error) {
        console.error("학생 수정 중 오류:", error);
        return c.json({ error: "학생 수정 중 오류가 발생했습니다." }, 500);
    }
})
    // DELETE /manage/student/:id - 학생 삭제 (ID를 문자열로 처리)
    .delete('/manage/student/:id', async (c) => {
    const user = c.get("user");
    if (!user) {
        return c.json({ error: "인증되지 않은 사용자입니다." }, 401);
    }
    const id = c.req.param('id'); // 문자열로 처리 (parseInt 제거)
    try {
        const [deletedStudent] = await db(c.env)
            .delete(students)
            .where(eq(students.id, id))
            .returning();
        if (!deletedStudent) {
            return c.json({ error: "학생을 찾을 수 없습니다." }, 404);
        }
        return c.json({ message: "학생이 삭제되었습니다." });
    }
    catch (error) {
        console.error("학생 삭제 중 오류:", error);
        return c.json({ error: "학생 삭제 중 오류가 발생했습니다." }, 500);
    }
});
